"use client";

import Image from "next/image";
import { Box, Container, Paper, Typography, Button, IconButton } from '@mui/material';
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import { styled } from '@mui/material/styles';
import { useState, useEffect } from 'react';
import { useRouter } from 'next/router'; // Import router for navigation

const StyledButton = styled(Button)({
  backgroundColor: '#E8E1C4', // Updated color for Change Password button
  color: '#777777',
  '&:hover': {
    backgroundColor: '#d8d0c0',
  },
  textTransform: 'none',
  boxShadow: 'none',
  padding: '8px 16px',
  borderRadius: '6px',
  fontWeight: 500,
  fontSize: '14px',
  minWidth: 'auto'
});

const ImageContainer = styled(Box)({
  width: 180,
  height: 180,
  borderRadius: '50%',
  overflow: 'hidden',
  position: 'relative',
  backgroundColor: '#F3F4F6',
  border: '4px solid #fff'
});

const CameraButton = styled(IconButton)({
  position: 'absolute',
  bottom: 15,
  right: 15,
  backgroundColor: '#E8E1C4', // Updated color for Camera button
  padding: '8px',
  width: 36,
  height: 36,
  zIndex: 2, // Ensures it appears above the image
  '&:hover': {
    backgroundColor: '#d8d0c0',
  }
});

const CustomContainer = styled(Container)({
  maxWidth: '1200px',
  padding: '0 24px',
  margin: '0 auto'
});

const details = [
  { label: "Email Address:", value: "infomail@exaple.com" },
  { label: "Communication preference", value: "Call, Email" },
  { label: "Phone no.:", value: "123 456 7890" },
  { label: "New Listing Frequency", value: "Daily" },
  { label: "Default Search City:", value: "Pleasant Hill" },
];

export default function ProfilePage() {
  const [isClient, setIsClient] = useState(false);
  const router = useRouter(); // Initialize router for navigation

  // Ensure that useRouter is only used on the client side
  useEffect(() => {
    setIsClient(true);
  }, []);

  const handleEditProfile = () => {
    if (isClient) {
      router.push('/edit-profile'); // Navigate to the edit profile page
    }
  };

  return (
    <Box sx={{ minHeight: '100vh', bgcolor: '#F9FAFB', py: 4 }}>
      <CustomContainer>
        <Paper
          elevation={0}
          sx={{
            borderRadius: '12px',
            boxShadow: '0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1)',
            bgcolor: '#fff',
            height: 'auto',
            minHeight: '280px' // Reduced height of the main container
          }}
        >
          <Box sx={{ p: 4, pt: 6 }}>
            <Box sx={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'flex-start',
              mb: -5,
              ml: 0.9,
              gap: 3
            }}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 4 }}>
                <Box
                  position="relative"
                  sx={{
                    mt: 2,
                    position: 'relative',
                    top: '65px', // No change: Image position remains the same
                    ml: 2.5,
                  }}
                >
                  <ImageContainer>
                    <Image
                      src="/api/placeholder/180/180"
                      alt="Profile"
                      width={180}
                      height={180}
                      style={{ objectFit: 'cover' }}
                      priority
                    />
                  </ImageContainer>
                  {/* Camera Icon positioned above the image */}
                  <CameraButton size="small">
                    <CameraAltIcon sx={{ fontSize: 18, color: '#374151' }} />
                  </CameraButton>
                </Box>
                <Typography
                  variant="h1"
                  sx={{
                    fontSize: '2rem',
                    fontWeight: 600,
                    color: '#484848',
                    lineHeight: 1.2,
                    ml: '32px' // Moves the name slightly to the right
                  }}
                >
                  John Doe
                </Typography>
              </Box>
              {/* Replace PersonIcon with dynamic Edit Profile button */}
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Button onClick={handleEditProfile}>
                  <Image
                    src="/public/Images/Edit-Profile-icon.svg"
                    alt="Edit Profile"
                    width={18}
                    height={18}
                  />
                </Button>
              </Box>
            </Box>

            {/* Details Section */}
            <Box sx={{
              display: 'grid',
              gridTemplateColumns: 'repeat(2, 1fr)',
              gap: 2.5,
              pl: '270px', // Matches the alignment of details with the name
              mt: -1,
              mb: 9
            }}>
              {details.map((detail, index) => (
                <Box
                  key={index}
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: 2
                  }}
                >
                  <Typography
                    sx={{
                      color: '#777777',
                      fontSize: '0.875rem',
                      fontWeight: 500,
                      width: '180px',
                      flexShrink: 0
                    }}
                  >
                    {detail.label}
                  </Typography>
                  <Typography
                    sx={{
                      color: '#777777',
                      fontSize: '0.875rem',
                      fontWeight: 500,
                      opacity: 0.7,
                    }}
                  >
                    {detail.value}
                  </Typography>
                </Box>
              ))}
            </Box>
          </Box>
        </Paper>
      </CustomContainer>
    </Box>
  );
}
